@import '../../../theme/scssVariables';

.verify-email {
  background-color: $primary;
  position: relative;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  .unicorn {
    min-width: 400px;
    max-width: 100%;
    height: 400px;
  }
  .container {
    max-width: 460px;
    _width: 400px;
    margin: 0 auto 80px;
    color: white;

    text-align: center;
  }

  .warning {
    margin: 30px 0 30px 0;
    padding: 0;
    color: white;
  }
  .warning p {
    font-size: 12px;
    font-size: 0.75rem;
    color: white;
  }

  .warning a {
    display: block;
    margin-top: 30px;
    padding: 20px;
    background: $primary;
    color: white;
    background-color: $primary-darken;
  }
  .warning h2 {
    color: white;
  }

  /* SVG */
  .unicorn {
    max-width: 100%;
    height: 400px;

    background: url(data:image/svg+xml;base64,)
      no-repeat center center;
  }

  .four-oh-four {
    max-width: 150px;
    height: 120px;
    text-indent: -9999px;
    margin: auto;

    background: url(data:image/svg+xml;base64,)
      no-repeat center center;
  }
  /* Responsive
-------------------------------------------------------*/

  /* Desktop only */
  @media only screen and (min-width: 1800px) {
    h2 {
      font-size: 28px;
      font-size: 1.75rem;
    }
    .warning p {
      font-size: 14px;
      font-size: 0.88rem;
    }
    .unicorn {
      max-width: 100%;
      height: 400px;
      margin: 60px 0 0 26px;
    }
  }

  @media only screen and (max-width: 568px) {
    .warning a {
      background: $primary-darken;
    }
    h2 {
      color: #fff;
    }
  }
  @media only screen and (max-width: 330px) {
    .unicorn {
      height: 250px;
      min-width: 250px;
      margin: 20px 0 0 0px !important;
    }
  }
  @media only screen and (max-width: 500px) {
    .unicorn {
      height: 300px;
      min-width: 300px;
      margin: 20px 0 0 0px !important;
    }

    h2 {
      font-size: 0.88rem;
      font-weight: bold;
    }
    .warning {
      margin: 0;
    }
    .warning p {
      margin-top: 10px;
      font-size: 0.63rem;
    }
    .warning a {
      margin-top: 20px;
      font-size: 0.63rem;
    }
  }
}
